import { Component,Renderer,EventEmitter, Output } from '@angular/core';
import { NavController,AlertController,ModalController,ViewController  } from 'ionic-angular';
import {Http} from '@angular/http';
import { HomePage } from '../../pages/home/home'
import {UniquePipe} from '../../app/unique.pipe'
import * as _ from 'lodash';

@Component( {
   selector: 'page-filter',
templateUrl: 'filter.html'
})
export class Profile {
  @Output() filterClosed = new EventEmitter();
 constructor(public viewCtrl: ViewController) {

 }

 public NoOfBathrooms = 0;
 public NoOfBedrooms = 0;
 public price = 0;
 public AreaSpace_SQFT = 0;
 private EstimatedRent = 0;
 public increment (type:any) {
   if(type == "bathroom")
   this.NoOfBathrooms ++;
   else
   this.NoOfBedrooms++;
 }
 
 public decrement (type:any) {
  if(type == "bathroom")
  {if(this.NoOfBathrooms > 0)
  this.NoOfBathrooms --;
  }
  else
  {if(this.NoOfBedrooms > 0)
  this.NoOfBedrooms-- 
}
}

 search(){
   var filter = [{
    searchType:'bedroom',
    searchValue:this.NoOfBedrooms
   },
   {
    searchType:'bathroom',
    searchValue:this.NoOfBathrooms
   },
   {
    searchType:'price',
    searchValue:this.price
   },
   {
    searchType:'AreaSpace_SQFT',
    searchValue:this.AreaSpace_SQFT
   },
   {
    searchType:'EstimatedRent',
    searchValue:this.EstimatedRent
   }   
  ]
  if(filter != undefined)
  { this.viewCtrl.dismiss(filter);}
  }

}

@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html'
})
export class ContactPage {

  data:any;
  originalData:any;
  data2:any = [];
  ZipData:any;
  City:any = [];
  Zipcode:any = [];
  City2:any = [];
  Zipcode2:any = [];
  CityData2:any;
  SelectedZipCodesToCompare:any = [];  
  StateData:any = [];
  //array of selected list items data
  selectedDataItems: any = [];
  // count of selected list items
  // if more than 2, compare button is set to visble
  selectedItemCount: number=0;
  // Toggle view type
  // True - Apartment List
  // False - Zip Code Search
  ListView:boolean = true;
  public rangeObject:any= {lower: 0, upper: 100};
  constructor(public navCtrl: NavController,private http:Http,private renderer: Renderer,public alertCtrl: AlertController, public modalCtrl: ModalController) {
    // get Json data from file
    debugger;
           this.http.get('./assets/data/housing_compare_app.json')
                   .subscribe(res => this.data = this.originalData = res.json());
  }

  //Highlight selected list item
  //Add data of list item to array
  //if exists, remove list item data from array and un highlight it.
  //prevent selection of more than 4 list items
  onItemSelect(data:any,event:any)
   { 
    var selectedId = event.currentTarget.id;
    // if number of seleted items is less than 4 and the selected list item is not in the array
     if(this.selectedItemCount <4 && this.selectedDataItems.find(x => x.Id == selectedId) == undefined)
     {
      this.selectedItemCount++;      
      this.selectedDataItems.push(data);
      //Add a class attribure so we can change the CSS of the selected item to make it Gray
      this.renderer.setElementClass(event.target,"selected",true);
    }

    //if the selected list item is in the array
     else if( this.selectedDataItems.find(x => x.Id == selectedId) != undefined)
     {
     this.selectedItemCount--; 
     //Remove the class 
     this.renderer.setElementClass(event.target,"selected",false);
     //remove Item from array with matching Id
     this.selectedDataItems.splice(this.selectedDataItems.findIndex(x => x.Id == selectedId),1);
    }
    //more than 4 selections
    else if(this.selectedItemCount >=4)
      {let alert = this.alertCtrl.create({
        title: 'Hey!',
        subTitle: 'At a maximum 4 items can be compared',
        buttons: ['OK']
      });
      alert.present();
    }
   }

   onItemLongPress(data:any,event:any)
   {
     debugger;
     var propertyDetails = ' <img src="https://static.pexels.com/photos/106399/pexels-photo-106399.jpeg"><br/><br/><b>Locality:</b> '+data.Locality + '<br/><b>Status: </b>'+data.Status+'<br/><b>Price: </b>'+data.Price+'<br/><b>Area Space: </b>'+data.AreaSpace_SQFT+ '<br/><b>Year Built: </b>'+data.YearBuilt;
     let alert = this.alertCtrl.create({
      title: '',
      subTitle: propertyDetails,
      buttons: ['OK']
    });
    alert.present();
     
   }

   onFilterDismissRefineList(filterData:any)
   {
     debugger;
      this.data = this.originalData;
      console.log(filterData);
      debugger;
      filterData.forEach(filterData => {

      if(filterData.searchType.toLowerCase() == "bedroom" &&  filterData.searchValue != "0")
      this.data =  this.data.filter(x => x.Bedrooms == filterData.searchValue)
      else if(filterData.searchType.toLowerCase() == "bathroom"  &&  filterData.searchValue != "0")
      this.data =  this.data.filter(x => x.Bathrooms == filterData.searchValue)
      else if(filterData.searchType.toLowerCase() == "price"  &&  filterData.searchValue != "0")
      {
           this.data =  this.data.filter(x => (x.Price > 0 && x.Price < filterData.searchValue))
   }
   else if(filterData.searchType.toLowerCase() == "areaspace_sqft"  &&  filterData.searchValue != "0")
   {
        this.data =  this.data.filter(x => (x.AreaSpace_SQFT > 0 && x.AreaSpace_SQFT < filterData.searchValue))
}
else if(filterData.searchType.toLowerCase() == "estimatedrent"  &&  filterData.searchValue != "0")
{
     this.data =  this.data.filter(x => (x.EstimatedRent > 0 && x.EstimatedRent < filterData.searchValue))
}
  });
   console.log(this.data);
   console.log(this.originalData);

   }

   filterList()
   {
      let profileModal = this.modalCtrl.create(Profile, { userId: 8675309 });
      profileModal.onDidDismiss(dataa => {
       this.onFilterDismissRefineList(dataa);
      });
      profileModal.present();
    }

   compareClick()
   {debugger;
    if(!this.ListView)
    {this.ListView=true;}
    else{
     if(this.selectedDataItems.length < 1)
     {
      if(this.ListView == true)
      { 
        let alert = this.alertCtrl.create({
          title: '',
          subTitle: 'Select at least 2 to compare',
          buttons: ['OK']
        });
        alert.present();
      
      } 
     }
     else
     {
      let alert = this.alertCtrl.create();
      alert.setTitle('Select properties to compare?');
  
      alert.addInput({
        type: 'checkbox',
        label: 'Area in SQ ft',
        value: 'AreaSpace_SQFT',
        checked: false
      });
  
      alert.addInput({
        type: 'checkbox',
        label: 'Price',
        value: 'Price'
      });

      alert.addInput({
        type: 'checkbox',
        label: 'Estimated Rent',
        value: 'EstimatedRent'
      });
  
      alert.addButton('Cancel');
      alert.addButton({
        text: 'Okay',
        handler: data => {       
          this.compareListData(data);
        }
      });
      alert.present();
    
    }
    }
   }

   compareListData(chartParams:any)
   {
    this.navCtrl.push(HomePage,{data:this.selectedDataItems,comparisonType:"list",chartParams:chartParams});    
   }

   saveZipcodeDataToCompare(selectedZipCode:any)  
   {
     debugger;
        if(this.SelectedZipCodesToCompare.indexOf(selectedZipCode) === -1)
        {
          this.SelectedZipCodesToCompare.push(selectedZipCode);
        }
      
   }




   compareZipDataClick()
   {debugger;

      if(this.SelectedZipCodesToCompare.length != 2)
      { 
        let alert = this.alertCtrl.create({
          title: '',
          subTitle: 'Select Zipcodes to compare',
          buttons: ['OK']
        });
        alert.present();   
      
     }
     else
     {
       debugger;
      var newArr = this.ZipData.filter(el => {
        if (this.SelectedZipCodesToCompare.indexOf(el.Zipcode) !== -1) {
            // If not present in array, then add it
            this.data2.push(el);
            return true;
        } else {
            // Already present in array, don't add it
            return false;
        }
    });  
     this.navCtrl.push(HomePage,{data:this.data2,comparisonType:"zipcode",chartParams:null});
     }
    }
   

   compareZipCodeClick()
   {
    
      this.ListView = false;
          // get Json data from file
          this.http.get('./assets/data/zip.json')
          .subscribe(
            res => this.ZipData = res.json(),
            error => console.log("Error getting Zipcode Data"),
            () => this.unique(this.ZipData)   
          );           
   }


    unique(arr:any){
      debugger;
      var newArr = arr.filter(el => {
        if (this.StateData.indexOf(el.state) === -1) {
            // If not present in array, then add it
            this.StateData.push(el.state);
            return true;
        } else {
            // Already present in array, don't add it
            return false;
        }
    });
    }


   populateCity(selectedState:any)
   {
    debugger;
    this.Zipcode = [];
    this.City = [];
    var newArr = this.ZipData.filter(el => {
      if (el.state.toUpperCase() == selectedState.toUpperCase() && this.City.indexOf(el.city) === -1) {
          // If not present in array, then add it
          this.City.push(el.city);
          return true;
      } else {
          // Already present in array, don't add it
          return false;
      }
  });
   }
   populateCity2(selectedState:any)
   {
    debugger;
    this.Zipcode2 = [];
    this.City2 = [];
    var newArr = this.ZipData.filter(el => {
      if (el.state.toUpperCase() == selectedState.toUpperCase() && this.City2.indexOf(el.city) === -1) {
          // If not present in array, then add it
          this.City2.push(el.city);
          return true;
      } else {
          // Already present in array, don't add it
          return false;
      }
  });
   }

   populateZip(selectedCity:any)
   {debugger; 
    var newArr = this.ZipData.filter(el => {
      if (el.city.toUpperCase() == selectedCity.toUpperCase() && this.Zipcode.indexOf(el.zip_code) === -1) {
          // If not present in array, then add it
          this.Zipcode.push(el.zip_code);
          return true;
      } else {
          // Already present in array, don't add it
          return false;
      }
  });
   }

   populateZip2(selectedCity:any)
   {debugger; 
    var newArr = this.ZipData.filter(el => {
      if (el.City.toUpperCase() == selectedCity.toUpperCase() && this.Zipcode2.indexOf(el.Zipcode) === -1) {
          // If not present in array, then add it
          this.Zipcode2.push(el.Zipcode);
          return true;
      } else {
          // Already present in array, don't add it
          return false;
      }
  });
   }

    find_in_object(my_object, my_criteria){
    
      return my_object.filter(function(obj) {
        return Object.keys(my_criteria).every(function(c) {
          return obj[c] == my_criteria[c];
        });
      });
    
    }

}
 


